.footer {
	width: 100vw;
	height: 5rem;
	background: grey;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.footer-links {
	display: -ms-flexbox;
	display: flex;
}

.footer-links > li {
	margin: 0 1rem;
}

.footer-links > li > a > i {
	font-size: 1.5rem;
	-webkit-transition: 0.4s ease-in-out;
	-o-transition: 0.4s ease-in-out;
	transition: 0.4s ease-in-out;
}

.footer-links > li > a:hover {
	color: #fff;
}



