.work {
	display: grid;
	justify-items: center;
	-ms-flex-align: center;
	    align-items: center;
	width: 100vw;
}

.work > ul {
	display: grid;
	grid-gap: 1rem;
	justify-items: center;
	-ms-flex-align: center;
	    align-items: center;
	margin: 4rem 0;
}

.work-content {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	padding: 1rem;
}

.details {
	width: 80vw;
	margin: auto;
}

.work-content a,
.link {
	text-decoration: underline;
	color: blue;
}

.work-content h4 {
	margin-bottom: 0;
}

.description {
	list-style-type: disc;
}

.description > li {
	margin-left: 2rem;
}

.screenshots {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.screenshots > li {
	width: 100%;
	margin: 1rem 0;
	-webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
	        box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.screenshots > li > img {
	width: 100%;
	-o-object-fit: cover;
	   object-fit: cover;
	height: 100%;
}

@media (min-width: 700px) {
	.work > ul {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-pack: distribute;
		    justify-content: space-around;
		-ms-flex-wrap: wrap;
		    flex-wrap: wrap;
	}

	.screenshots {
		-ms-flex-direction: row;
		    flex-direction: row;
		-ms-flex-wrap: wrap;
		    flex-wrap: wrap;
		-ms-flex-pack: justify;
		    justify-content: space-between;
	}

	.screenshots > li {
		width: 48%;
	}
}



